.city {
    padding: 55px 0;

    &__title {
        &-wrap {
            margin-bottom: 40px;
        }

        &-desc {
            background-color: var(--white);
            max-width: 737px;
            padding: 0 35px;
        }
    }

    &__box {
        &:not(:last-child) {
            margin-bottom: 40px;
        }
    }

    &__container {
        display: flex;
        justify-content: center;
    }

    &__content {
        max-width: 830px;
        width: 100%;
        display: flex;
        align-items: center;
        flex-direction: column;
    }

    &__block {
        max-width: 783px;
        width: 100%;
        &:not(:last-child) {
            margin-bottom: 40px;
        }

        &-list {
            max-width: 830px;
        }
    }

    &__list {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__item {
        position: relative;
    }

    &__info-item {
        &:not(:last-child) {
            margin-bottom: 5px;
        }
    }

    &__nowrap {
        white-space: nowrap;
        display: inline-block;
    }
}


@media screen and (max-width: 639px) {
    .city {
        padding: 40px 0;

        &__title-wrap {
            margin-bottom: 35px;
        }

        &__block {
            &:not(:last-child) {
                margin-bottom: 30px;
            }
        }
    }
}